.fade-in-linear-enter-active,
.fade-in-linear-leave-active {
  transition: opacity 200ms linear; }

.fade-in-linear-enter,
.fade-in-linear-leave,
.fade-in-linear-leave-active {
  opacity: 0; }

/** tu-fade-in-linear */
.tu-fade-in-easeout-enter-active,
.tu-fade-in-easeout-leave-active {
  transition: opacity 0.3s ease-out; }

.tu-fade-in-easeout-enter,
.tu-fade-in-easeout-leave,
.tu-fade-in-easeout-leave-active {
  opacity: 0; }

/** tu-fade-in */
.tu-fade-in-enter-active,
.tu-fade-in-leave-active {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-fade-in-enter,
.tu-fade-in-leave-active {
  opacity: 0; }

/** tu-popper-in-center */
.tu-popper-in-center-enter-active,
.tu-popper-in-center-leave-active {
  transition: all 0.2s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-popper-in-center-enter,
.tu-popper-in-center-leave-active {
  scale: 0;
  transform: scaleX(0); }

/** tu-zoom-in-center */
.tu-zoom-in-center-enter-active,
.tu-zoom-in-center-leave-active {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-zoom-in-center-enter,
.tu-zoom-in-center-leave-active {
  opacity: 0;
  transform: scaleX(0); }

/** tu-zoom-in-top */
.tu-zoom-in-top-enter-active,
.tu-zoom-in-top-leave-active {
  opacity: 1;
  transform: scaleY(1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: center top; }

.tu-zoom-in-top-enter,
.tu-zoom-in-top-leave-active {
  opacity: 0;
  transform: scaleY(0); }

.tu-zoom-in-bottom-enter-active,
.tu-zoom-in-bottom-leave-active {
  opacity: 1;
  transform: scaleY(1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: center bottom; }

.tu-zoom-in-bottom-enter,
.tu-zoom-in-bottom-leave-active {
  opacity: 0;
  transform: scaleY(0); }

/** tu-zoom-in-left */
.tu-zoom-in-left-enter-active,
.tu-zoom-in-left-leave-active {
  opacity: 1;
  transform: scale(1, 1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: top left; }

.tu-zoom-in-left-enter,
.tu-zoom-in-left-leave-active {
  opacity: 0;
  transform: scale(0.45, 0.45); }

.collapse-transition {
  transition: 0.3s height ease-in-out, 0.3s padding-top ease-in-out, 0.3s padding-bottom ease-in-out; }

.horizontal-collapse-transition {
  transition: 0.3s width ease-in-out, 0.3s padding-left ease-in-out, 0.3s padding-right ease-in-out; }

.tu-list-enter-active,
.tu-list-leave-active {
  transition: all 0.48s; }

.tu-list-enter,
.tu-list-leave-active {
  opacity: 0;
  transform: translateX(-40px); }

.tu-opacity-transition {
  transition: opacity 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

html,
body {
  height: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

body {
  -webkit-font-smoothing: antialiased;
  font-size: 14px;
  color: #9baacf;
  -webkit-font-smoothing: antialiased; }

/* BEM [block]__[element]--[modifier]
 -------------------------- */
@keyframes tu-drawer-fade-in {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes rtl-drawer-in {
  0% {
    transform: translate(100%, 0px); }
  100% {
    transform: translate(0px, 0px); } }

@keyframes rtl-drawer-out {
  0% {
    transform: translate(0px, 0px); }
  100% {
    transform: translate(100%, 0px); } }

@keyframes ltr-drawer-in {
  0% {
    transform: translate(-100%, 0px); }
  100% {
    transform: translate(0px, 0px); } }

@keyframes ltr-drawer-out {
  0% {
    transform: translate(0px, 0px); }
  100% {
    transform: translate(-100%, 0px); } }

@keyframes ttb-drawer-in {
  0% {
    transform: translate(0px, -100%); }
  100% {
    transform: translate(0px, 0px); } }

@keyframes ttb-drawer-out {
  0% {
    transform: translate(0px, 0px); }
  100% {
    transform: translate(0px, -100%); } }

@keyframes btt-drawer-in {
  0% {
    transform: translate(0px, 100%); }
  100% {
    transform: translate(0px, 0px); } }

@keyframes btt-drawer-out {
  0% {
    transform: translate(0px, 0); }
  100% {
    transform: translate(0px, 100%); } }

.tu-drawer {
  position: absolute;
  box-sizing: border-box;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  outline: 0; }
  .tu-drawer.rtl {
    animation: rtl-drawer-out 0.3s; }
  .tu-drawer__open .tu-drawer.rtl {
    animation: rtl-drawer-in 0.3s 1ms; }
  .tu-drawer.ltr {
    animation: ltr-drawer-out 0.3s; }
  .tu-drawer__open .tu-drawer.ltr {
    animation: ltr-drawer-in 0.3s 1ms; }
  .tu-drawer.ttb {
    animation: ttb-drawer-out 0.3s; }
  .tu-drawer__open .tu-drawer.ttb {
    animation: ttb-drawer-in 0.3s 1ms; }
  .tu-drawer.btt {
    animation: btt-drawer-out 0.3s; }
  .tu-drawer__open .tu-drawer.btt {
    animation: btt-drawer-in 0.3s 1ms; }
  .tu-drawer__wrapper {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: hidden;
    margin: 0; }
  .tu-drawer__header {
    padding: 12px 18px;
    align-items: center;
    color: #8394be;
    font-size: 14px;
    font-weight: 600;
    display: flex;
    line-height: 22px;
    border-bottom: 1px solid rgba(190, 200, 228, 0.38); }
    .tu-drawer__header > :first-child {
      flex: 1; }
  .tu-drawer__title {
    margin: 0;
    flex: 1;
    line-height: inherit;
    color: #8394be;
    font-size: 14px;
    font-weight: 600; }
  .tu-drawer__close-btn {
    border: none;
    cursor: pointer;
    font-size: 16px;
    color: inherit;
    background-color: transparent;
    transition: color 0.3s; }
    .tu-drawer__close-btn:focus, .tu-drawer__close-btn:hover {
      color: #6d5dfc; }
  .tu-drawer__body {
    flex: 1;
    overflow: auto;
    padding: 24px 18px;
    color: #8394be;
    font-size: 14px;
    word-break: break-all; }
    .tu-drawer__body > * {
      box-sizing: border-box; }
  .tu-drawer.ltr, .tu-drawer.rtl {
    height: 100%;
    top: 0;
    bottom: 0; }
  .tu-drawer.ttb, .tu-drawer.btt {
    width: 100%;
    left: 0;
    right: 0; }
  .tu-drawer.ltr {
    left: 0; }
  .tu-drawer.rtl {
    right: 0; }
  .tu-drawer.ttb {
    top: 0; }
  .tu-drawer.btt {
    bottom: 0; }

.tu-drawer__container {
  position: relative;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  height: 100%;
  width: 100%; }

.tu-drawer-fade-enter-active {
  animation: tu-drawer-fade-in 0.3s; }

.tu-drawer-fade-leave-active {
  animation: tu-drawer-fade-in 0.3s reverse; }
